

// customer부분
.Qna_rollingpage{
    width: 100%;
    //border: 1px solid #252C32;
    box-sizing: border-box;
}

.Qna_custo_top_cate{
    display: flex;
    width: 100%;
    height: 30px;
    border-bottom: 1px solid #E5E9EB;
    justify-content: flex-start;
}

.Qna_custo_top_cate > li{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    color: #84919A;
    margin-right: 10px;
}

.Qna_custo_top_cate > li:nth-of-type(1){
    width: 80px;
}

.Qna_custo_top_cate > li:nth-of-type(2){
    width: 240px;
}

.Qna_custo_top_cate > li:nth-of-type(3){
    width: calc(100% - 360px);
    margin-right: 0px;
}

.Qna_custo_divbox{
    margin-top:8px;
    width: 100%;
    height: auto;
    padding-bottom: 8px;
    border-bottom: 1px solid #E5E9EB;
}

.Qna_custo_divbox > ul {
    width: 100%;
    height: auto;
    display: flex;
    font-family: 'Inter', sans-serif;
    font-size: 14px;
}

.Qna_custo_divbox > ul  > li{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    margin-right: 10px;
}

.Qna_custo_divbox > ul  > li:nth-of-type(1){
    width: 80px;
    height: 30px;
    background: #4094F7;;
    color:white;
    font-weight: 600;
}

.Qna_custo_divbox > ul > li:nth-of-type(2){
    width: 240px;
    height: 30px;
    background: #EBFFF1;;
    color:#252C32;
    font-weight: 400;
}

.Qna_custo_divbox > ul  > li:nth-of-type(3){
    width: calc(100% - 360px);
    margin-right: 0px;
    background: #F5F5F5;;
    color:#252C32;
    padding: 10px;
    font-size: 12px;
    position: relative;
    justify-content: flex-start;
}

.Qna_custo_divbox > ul  > li:nth-of-type(3) > ul{
    position: absolute;
    right:10px;
    top: 10px;
}

.Qna_custo_divbox > ul  > li:nth-of-type(3) > ul > li{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 63px;
    height: 24px;
    float: left;
    border-radius: 8px;
    opacity: 0.7;
    font-weight: 700;
    margin-left:10px;
    background: #4094F7;
    color:white;
    cursor: pointer;
    transition: all 0.3s;

    
        &:hover{
            opacity: 10;
        }
}
.Qna_custo_divbox > ul  > li:nth-of-type(3) > ul > li > a{
        color:white;    
}

.Qna_custo_divbox > ul  > li:nth-of-type(3) > ul > li:nth-of-type(2){
    background: #EBFFF1;
}

.Qna_custo_divbox > ul  > li:nth-of-type(3) > ul > li:nth-of-type(2) > a{
    color:#119C2B;
}

