.chanswerContainer {
    overflow: hidden;
    position: absolute;
    width: auto;
    height: auto;
    z-index: 1;
    background-color: #ffffff;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    margin-left: 100%;
    margin-top: -1px;
    left: -550px;
    border-radius: 15px 0px 15px 15px;
  }
  
  .chanswer {
    width: 390px;
    height: 0px;
    opacity: 0;
    transition: height 0.5s, opacity 1s;
  }
  
  .chanswer.open {
    height: auto;
    opacity: 1;
    width: 390px;
    height: 550px;
}