.newdetail_div{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.newdetail_top{
    width: 100%;
    height: 40px;
    background-color: #EBFFF1;
    text-align: right;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.newdetail_top > a {
    margin-left:10px;
    margin-top:3px;
    padding-right: 40px;
    color: #119C2B;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
}

.newdetail_ul{
    z-index:1;
    width: 350px;
    height: auto;
    border: 1px;
    margin-top: 20px;
}

.newdetail_ul > li{
    width: 100%;
    height: 28px;
    padding-bottom: 9px;
    padding-top: 9px;
    border-bottom: 1px solid #dfdfdf;
    display: flex;
    align-items: center;
}

.newdetail_ul > li:nth-of-type(3){
    width: 100%;
    height: 280px;
}

.newdetail_Newwrite > path{
    fill:#119C2B
}

.newdetail_ul > li > a {
    z-index:0;
    width: 50px;
    height: inherit;
    color: #707070;
    font-size: 13px;
    font-weight: 400;
    margin-right: 20px;
    display: flex;
    align-items: flex-start;
    margin-top: 5px;
    justify-content: flex-end;
}

.newdetail_ul > li > input{
    width: 280px;
    height: 28px;
    border: none;
    background-color: #EFEFEF;
    border-radius: 10px;
    text-indent: 10px;
}

.newdetail_ul > li:nth-of-type(3) > textarea{
    width: 280px;
    border: none;
    background-color: #EFEFEF;
    border-radius: 10px;
    height: 280px;
    resize: none;
    padding: 10px;
    box-sizing: border-box;
}

.newdetail_btn_div{
    z-index:1;
    width: 350px;
    height: 32px;
    display: flex;
    justify-items: flex-start;
    margin-top: 10px;
    margin-bottom: 10px;
}

.newdetail_btn_div > button{
    width: 130px;
    height: 32px;
    background-color: #119C2B;
    border: none;
    border-radius: 10px;
    color:#ffffff;
    font-size: 14px;
    font-weight: 700;
    margin-left: 5px;
    opacity: 0.7;
    cursor: pointer;
}

.newdetail_btn_div > button:hover{
    opacity: 1;
}

.newdetail_btn_div > button:last-child{
    width: 85px;
    height: 32px;
    background-color: #777777;
    border: none;
    border-radius: 10px;
    color:#ffffff;
    font-size: 14px;
    font-weight: 700;
    margin-left: 5px;
}


.loadingcss2 {
    z-index:0;
    width: 100%;
    margin-top: 50px;
    height: 100%;
    background-color: #ffffff;
    position: absolute;
    opacity: 0;
    transition: 1s;
  }
  
  .loadingcss2.isuploding {
    z-index:2;
    opacity: 1; /* .loadingcss2.isuploding 클래스가 적용될 때 투명도를 1로 변경 */
    transition: 1s; /* opacity 속성 변경 시 2초 동안 트랜지션 효과 적용 */
  }
  
  .loadingcss {
    position: relative;
    margin-left: 43%;
    width: 7%;
    top: 40%;
    object-fit: contain;
    transition: 1s; /* .loadingcss 클래스에 transition 속성 추가 */
  }
  
  .indexerror{
    position: absolute;
    margin-top:45%;
    z-index:3;
    width: 300px;
    height: 200px;
    background-color: #ff0000a2;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .errorimg{
    fill: #ffffff;
    width: 50px;
    height: 50px;
  }

  .indexerror > a{
    margin-top: 15px;
    text-align: center;
    color: #ffffff;
  }

  .indexerror > button{
    margin-top:15px;
    width: 50px;
    height: 25px;
    border: none;
    background-color: #ffffff;
    border-radius: 10px;
  }
