.Reserve_rollingpage{
    width: 100%;
    min-width: 1080px;
    //border: 1px solid #252C32;
    box-sizing: border-box;
}

.Reserve_index_top_cate{
    display: flex;
    width: 100%;
    height: 30px;
    min-width: 1080px;
    border-bottom: 1px solid #E5E9EB;
    justify-content: flex-start;   
}

.Reserve_index_top_cate > li{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    color: #84919A;
    height: 30px;
}

.Reserve_index_top_cate > li:nth-of-type(1){
    width: 40px;
}

.Reserve_index_top_cate > li:nth-of-type(2){
    width: 60px;
}

.Reserve_index_top_cate > li:nth-of-type(3){
    width: 170px;
}

.Reserve_index_top_cate > li:nth-of-type(4){
    width: 70px;
}

.Reserve_index_top_cate > li:nth-of-type(5){
    width: 210px;
}

.Reserve_index_top_cate > li:nth-of-type(6){
    width: 130px;
}

.Reserve_index_top_cate > li:nth-of-type(7){
    width: 130px;
}

.Reserve_index_top_cate > li:nth-of-type(8){
    width: calc( 100% - 910px);
    min-width: 180px;
}

.Reserve_index_top_cate > li:nth-of-type(9){
    width: 100px;
}

.Reserve_index_top_cate{
    margin-top:8px;
    width: 100%;
    height: auto;
    padding-bottom: 8px;
    border-bottom: 1px solid #E5E9EB;
}

.Reserve_index_top_cate > ul {
    width: 100%;
    min-width: 1080px;
    height: auto;
    display: flex;
    align-items: flex-start;
    font-family: 'Inter', sans-serif;
    font-size: 14px;
}

.Reserve_index_top_cate > ul  > li{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 8px;
    height: auto;
}

.Reserve_index_top_cate > ul > li:nth-of-type(1){
    width: 40px;
    height: 62px;
}

.Reserve_index_top_cate > ul > li:nth-of-type(2){
    width: 60px;
    height: 62px;
}

.Reserve_index_top_cate > ul > li:nth-of-type(2) > a{
    width: 60px;
    height: 60px;
    border-radius: 8px;
    text-align: center;
    line-height: 60px;
    background-color: #F5F5F5;
    color:#84919A;
    font-weight: 700;
}

.Reserve_index_top_cate > ul > li:nth-of-type(3){
    width: 170px;
    height: 62px;
}

.Reserve_index_top_cate > ul > li:nth-of-type(3) > div{
    width: 140px;
    height: 25px;
    text-align: center;
    display: flex;
    align-items: center;
}

.Reserve_index_top_cate > ul > li:nth-of-type(3) > div:nth-of-type(1){
border-bottom: 1px solid gray;
justify-content: center;
}

.Reserve_index_top_cate > ul > li:nth-of-type(3) > div:nth-of-type(2){
    justify-content: space-between;
}

.Reserve_index_top_cate > ul > li:nth-of-type(3) > div:nth-of-type(2) > a:first-child{
    font-weight: 700;
}

.Reserve_index_top_cate > ul > li:nth-of-type(4){
    width: 60px;
    height: 62px;
    margin-left : 5px;
    margin-right:  5px;
}

.Reserve_index_top_cate > ul > li:nth-of-type(4) > div{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 60px;
    height: 50px;
    background-color: #ebfff1;
    border-radius: 8px;
}

.Reserve_index_top_cate > ul > li:nth-of-type(4) > div > a{
    color:green;
    font-weight: 600;
}


.Reserve_index_top_cate > ul > li:nth-of-type(5){
    width: 210px;
}

.Reserve_index_top_cate > ul > li:nth-of-type(5) > div:first-child , .Reserve_index_top_cate > ul > li:nth-of-type(5) > div:last-child{
    width: 190px;
    font-weight: 700;
    text-align: center;
}

.data_routecss{
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.data_routecss.open{
    white-space: unset;
    text-overflow: unset;
    overflow: hidden;
}

.Reserve_index_top_cate > ul > li:nth-of-type(5) > div > a{
    color: #5B636A;
}

.Reserve_index_top_cate > ul > li:nth-of-type(5) > div:nth-of-type(2){
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    color: #5B636A;
    margin-top: 3px;
    margin-bottom: 3px;
}

.Reserve_index_top_cate > ul > li:nth-of-type(5) > div:nth-of-type(2) > a {
    background-color: #4094f7;
    border-radius: 6px;
    color: white;
    height: 20px;
    display: flex;
    align-items: center;
}

.Reserve_index_top_cate > ul > li:nth-of-type(5) > div:nth-of-type(2) > a:first-child {
    padding-left:4px;
    width: 130px;
    cursor: pointer;
}

.Reserve_index_top_cate > ul > li:nth-of-type(5) > div:nth-of-type(2) > a:first-child:hover {
    background-color: #288cff;
}

.Reserve_index_top_cate > ul > li:nth-of-type(5) > div:nth-of-type(2) > a:nth-of-type(2) {
    padding-left:2px;
    padding-right: 2px;
    justify-content: center;
    width: 50px;
}

.stop_spancss{
    width: 190px;
    height: 0;
    opacity: 0;
    border-radius: 6px;
   
    color:white;
    background-color: #4094f7;
    transition: height 0.5s, opacity 0.5s;
}

.stop_spancss.open{
    margin-top:3px;
    padding: 8px;
    height: auto;
    opacity: 1;
    width: 190px;
}


.stopdatacss{
    margin-bottom: 5px;
}

.stopdatacss:last-child{
    margin-bottom: 0px;
}


.Reserve_index_top_cate > ul > li:nth-of-type(5) > div:nth-of-type(2){
    width: 190px;
    font-size: 12px;
}

.Reserve_index_top_cate > ul > li:nth-of-type(5) > div:nth-of-type(2) > a:first{
    width: 190px;
    font-size: 12px;
}

.Reserve_index_top_cate > ul > li:nth-of-type(6),.Reserve_index_top_cate > ul > li:nth-of-type(7){
    width: 130px;
}

.Reserve_index_top_cate > ul > li:nth-of-type(6) > div , .Reserve_index_top_cate > ul > li:nth-of-type(7) > div{

    overflow: hidden;
    overflow-y: auto;
    background-color: #f5f5f5;
    width: 120px;
    height: 30px;
    text-align: center;
    padding: 5px;
    box-sizing: border-box;
    border-radius: 8px;
    font-size: 12px;
    font-weight: 500;
    line-height: 20px;
    color:#535C63;
}

.Reserve_index_top_cate > ul > li:nth-of-type(6) > div:first-child , .Reserve_index_top_cate > ul > li:nth-of-type(7) > div:first-child{
    margin-bottom: 10px;
}

.Reserve_index_top_cate > ul > li:nth-of-type(8){
    margin-left:5px;
    width: calc( 100% - 905px);
    justify-content: flex-start;
}

.Reserve_index_top_cate > ul > li:nth-of-type(8) > div{
    width: 100%;
    height: 70px;
    background-color: #f5f5f5;
    padding: 10px;
    border-radius: 8px;
    box-sizing: border-box;
    font-size: 12px;
    overflow-y: auto;
}

.Reserve_index_top_cate > ul > li:nth-of-type(9){
    width: 100px;
}

.Reserve_index_top_cate > ul > li:nth-of-type(9)> a{
    width: 70px;
    height: 30px;
    border-radius: 8px;
    line-height: 30px;
    font-size: 14px;
    background-color: #ebfff1;
    color: #119C2B;
    font-weight: 700;
    text-align: center;
}

.Reserve_index_top_cate > ul > li:nth-of-type(9)> a:first-child{
    margin-bottom: 10px;
}



.loadingcss2{
    width: 100%;
    height: 100%;
    background-color: #ffffff67;
}

.loadingcss{
    position: relative;
    margin-left:43%;
    width: 7%;
    top: 40%;
    object-fit: contain;
}


.chanswerContainer {
    overflow: hidden;
    position: absolute;
    width: auto;
    height: auto;
    z-index: 1;
    background-color: #ffffff;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    margin-left: 100%;
    margin-top: -1px;
    left: -550px;
    border-radius: 15px 0px 15px 15px;
  }
  
  .chanswer {
    width: 390px;
    height: 0px;
  }
  
  .chanswer.open {
    height: auto;
}