@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap');

.reservepage{
    width: 100%;
    min-width: 1018px;
    height: 100%;
    position: relative;
}

.reserve_top{
    margin-top: 30px;
    margin-left: 30px;
    font-size: 39px;
    font-style: normal;
    font-weight: 700;
    color: #84909A;
}

.page_icon{
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

.reserve_index{
    margin-left:30px;
    margin-top:30px;
    width: calc(100% - 60px);
    height: calc(100% - 125px);
    /* border: 1px solid gray; */
}

.reserve_menu{
    width: 100%;
    height: 30px;
    border-bottom: 1px solid #E5E9EB;
    display: flex;
    justify-content: space-between;
}

.reserve_menu_slider_ul{
    width: auto;
    height: 30px;
    display: flex;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
}

.reserve_menu_slider_ul > li{
    position: relative;
    margin-left:24px;
    margin-right: 24px;
    height: 30px;
    cursor: pointer;
    transition: .6s ease-out;
}

.reserve_menu_slider_ul > li:first-child {
    margin-left: 0;
}

.slider_line{
    position: absolute;
    width: 48px;
    height: 1px;
    background-color: black;
    transition: .4s ease-out;
}


.reserve_menu_seach_ul{
    margin-top: -15px;
    width: auto;
    height: 30px;
    display: flex;
}

.reserve_menu_seach_ul > li{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    margin-left: 10px;
    position: relative;
}

.reserve_menu_seach_ul > li:nth-of-type(1){
    color:#252C32;
    width: 110px;
    height: 30px;
    border-radius: 6px;
    border: 1px solid #DDE2E4;
    position: relative;
    cursor: pointer;
    justify-content: space-between;
}

.reserve_menu_seach_ul > li:nth-of-type(1) > a{
    font-size: 13px;
    margin-right: 12px;
    font-weight: 600;
}

.reserve_menu_seach_ul > li:nth-of-type(1) svg{
    width: 10px;
    margin-left: 10px;
    margin-right: 6px;
}


.reserve_menu_seach_ul > li:nth-of-type(2){
    color:#252C32;
    width: 280px;
    height: 30px;
    border-radius: 6px;
    border: 1px solid #DDE2E4;
    justify-content: flex-start;
}

.reserve_menu_seach_ul > li:nth-of-type(2) svg{
    width: 12px;
    margin-left: 8px;
    margin-right: 8px;
}

.reserve_menu_seach_ul > li:nth-of-type(2) > input[type="text" i] {
    border: none;
    color:#84909A;
    padding: 0;
    margin:0;
    box-sizing: border-box;
}

.reserve_menu_seach_ul > li:nth-of-type(2) > input:focus {
    outline: none;
}

.reserve_menu_seach_ul > li:nth-of-type(3) , .reserve_menu_seach_ul > li:nth-of-type(4) , .reserve_menu_seach_ul > li:nth-of-type(5) , .reserve_menu_seach_ul > li:nth-of-type(6){
    color: #F6F8F9;
    width: 60px;
    height: 30px;
    background-color: #4094F7;
    border-radius: 6px;
    font-weight: 600;
    cursor: pointer;
    position: relative;
}


.datechoice{
    width: auto;
    height: 25px;
    display: flex;
    position: absolute;
    margin-right: 100%;
    right: -100%;
    margin-top: -130%;
    padding: 5px;
    border-radius: 8px;
    background-color: #f5f5f5;
}

.datechoice > li {
    width: 40px;
    height: 25px;
    color: #252C32;
    margin-left:3px;
    text-align: center;
    line-height: 25px;
    border-radius: 8px;
    background-color: #cadff2;
}

.datechoice > li:last-child {
    width: 165px;
    background-color: #f5f5f5;
}

.search_arrow {
    position: absolute;
    margin-top:50%;
    top:-70%;
    width: 100%;
    border-radius: 0.5rem;
    border: 2px solid #DDE2E4;
    padding: 0.5rem 0.5rem;
    background: white;
    list-style: none;
    box-sizing: border-box;
    z-index: 1;
  
    .search_button {
      display: block;
      width: 100%;
      text-align: right;
      transition: all 0.1s;
      padding: 0.5rem;
      border-radius: 0.5rem;
      border: 0;
      outline: 0;
      background: none;
  
      &:hover {
        background: #eee;
      }
      &:active {
        background: #aaa;
      }
      &.selected {
        background: #4d4d4d;
        color: #e4e4e4;
      }
    }
  }


  /* 초기 상태 - 아이콘 회전하지 않음 */
.filter_icon, .filter_icon path {
  transform: scaleX(1);
  fill: #5B6871;
  transition: transform 0.5s, fill 0.5s;
}

/* 클릭하면 아이콘 회전 및 색상 변경 */
.filter_icon.active {
  transform: scaleX(-1); /* 360도 회전 */
  
}

.filter_icon.active path {
  fill: #4094F7; /* 색상 변경 */
}


.reserve_pagelist_div{
    width: 100%;
    height: 94%;
    overflow: hidden;
}
.reserve_pagelist {
    position: relative;
    display: flex; /* Flexbox 사용 */
    height: 100%;
    transition: .6s ease-out;
}
  
.reserve_pagelist > li {
    width: 100%;
    overflow-y: scroll;
}
.loadingcss2{
    width: 100%;
    height: 100%;
    background-color: #ffffff67;
}

.loadingcss{
    position: relative;
    margin-left:43%;
    width: 7%;
    top: 40%;
    object-fit: contain;
}

.box{
    width: 50px;
    height: 50px;
    background-color: #4094F7;
}