.nav{
  width: 240px;
  height: 100%;
  background: #F6F8F9;
  border-right: 1px solid #E5E9EB;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
}

.nav_innerline{
  width: 200px;
  height: 95%;
  display: flex;
  flex-direction: column;
}

.nav_ul_list > li:hover{   
  background-color: #D7EDFF;
  color : #0E73F6;
  font-weight: 700;
  transition: background-color 0.3s;
}

.img_logo{
  width: 25px;
  height: 25px;
  border-radius: 5px;
}

.nav_title{
  width: auto;
  height: 25px;
  line-height: 25px;
  margin-left: 5px;
  font-size: 18px;
  font-weight: bold;
  color: #232323;
}

.nav_ul_list{
  margin-top:25px;
  width: 200px;
  height: auto;
}

.nav_ul_list > li{
  border-radius: 10px;
  width: 200px;
  height: 32px;
  font-size: 14px;
  font-weight: 400;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.tag_link > svg {
  margin-left: 5px;
  margin-right: 8px;
}

.tag_link:hover .nav_icon path {
  fill: #0E73F6;
  transition: fill 0.3s;
}

.tag_link{
  width: 100%;
  height: 100%;
  text-decoration: none;
  color:#252C32;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.tag_link:hover{
  color : #0E73F6;
  font-weight: 700;
  transition: background-color 0.3s;
}
